            .div(titulo="Visualizaci√≥n estrat√©gica de la informaci√≥n")
              p Los recursos gr√°ficos deben usarse no solo para embellecer, sino para facilitar la comprensi√≥n y la acci√≥n. Se deben evitar gr√°ficos decorativos y priorizar formatos que resalten patrones, relaciones y anomal√≠as.<br><br><b>Ejemplo.</b> Para mostrar la distribuci√≥n geogr√°fica de ventas, un mapa de calor es m√°s √∫til que una tabla con cifras, por departamento. El uso de colores debe responder a un criterio l√≥gico (verde = bueno, rojo = alerta).

      .row.mb-4
