                p.text-bold Comparaci√≥n hist√≥rica y metas
                p El dise√±o debe permitir contrastar el valor actual del indicador, con su hist√≥rico y con la meta planteada. Esto refuerza el enfoque de mejora continua.<br><br><b>Ejemplo.</b> Un gr√°fico de l√≠nea de satisfacci√≥n del cliente compara el valor actual (82 %) con el valor del mes anterior (79 %) y la meta corporativa (85 %).

