            .div(titulo="Datos claves (evidencia)")
              p Aqu√≠ se presentan los hallazgos relevantes mediante visualizaciones claras. Se eligen los datos que aportan valor a la narrativa, evitando el exceso de m√©tricas.<br><br><b>Ejemplo.</b> Mostrar un gr√°fico de dispersi√≥n donde se observa una correlaci√≥n entre la baja interacci√≥n en la plataforma y la tasa de cancelaci√≥n.

            .div(titulo="Interpretaci√≥n")
              p No basta con mostrar el dato, hay que traducirlo. ¬øQu√© significa ese hallazgo? ¬øQu√© implicaciones tiene?<br><br><b>Ejemplo.</b> Los usuarios con menos de 3 accesos semanales, tienen el doble de probabilidad de cancelar su suscripci√≥n.

